.rn-content-wrapper {
    position: relative;
    z-index: 5;
    .padding-tb {
        padding: 20px;
        background: #212428;
        border-radius: 10px;
        &.m_dec-top {
            margin-top: -100px;
            margin-left: 0;
            margin-right: 0;
        }
    }
    .header-left {
        display: flex;
        align-items: center;
        position: relative;
        @media #{$smlg-device} {
            flex-direction: column;
            text-align: center;
        }
        .header-thumbnail {
            border-radius: 15px;
            max-height: 200px;
            border: 5px solid #1d1e24;
            position: relative;
            top: -80px;
        }
        .header-info-content {
            margin-left: 30px;
            @media #{$smlg-device} {
                margin-left: 0;
                margin-top: -46px;
            }
            .social-share-style-1 {
                margin-top: 15px !important;
                @media #{$smlg-device} {
                    margin-left: 0;
                    margin-top: -50px;
                    text-align: center;
                }
                &.border-none {
                    @media #{$smlg-device} {
                        justify-content: center;
                        display: flex;
                        margin-top: 5px !important;
                    }
                }
            }
            h4 {
                font-size: 32px;
                margin-bottom: 10px;
                @media #{$large-mobile} {
                    font-size: 24px;
                }
            }
            .status-info {
                font-size: 18px;
                font-weight: 500;
            }
        }
        &::before {
            content: "";
            background-color: #151517;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            height: 100%;
            width: 1px;
            @media #{$md-layout} {
                content: "";
                background-color: #151517;
                position: absolute;
                top: 106%;
                right: 0;
                bottom: -13px;
                height: 1px;
                width: 100%;
            }
            @media #{$sm-layout} {
                content: "";
                background-color: #151517;
                position: absolute;
                top: 106%;
                right: 0;
                bottom: -13px;
                height: 1px;
                width: 100%;
            }
        }
    }
    .header-right {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        @media #{$md-layout} {
            margin-top: 30px;
        }
        @media #{$sm-layout} {
            flex-direction: column;
            margin-top: 30px;
        }
    }
}

