.social-area {
    .name {
        font-size: 14px;
        font-family: var(--font-secondary);
        font-weight: 500;
        color: var(--color-body);
        margin-bottom: 8px;
    }
    .social-icone {
        margin-top: 24px;
        a {
            width: 61px;
            height: 61px;
            line-height: 57px;
            margin-right: 15px;
            box-shadow: var(--shadow-1);
            border-radius: 6px;
            transition: var(--transition);
            display: inline-block;
            text-align: center;
            &::before {
                content: "";
                position: absolute;
                transition: var(--transition);
                width: 100%;
                height: 100%;
                border-radius: 6px;
                top: 0;
                left: 0;
                background: linear-gradient(
                    to right bottom,
                    #212428,
                    #16181c
                );
                opacity: 0;
                z-index: -1;
            }
            svg {
                stroke-width: 2;
                width: 20px;
                color: var(--color-lightn);
                transition: var(--transition);
            }
            &:hover {
                transform: translateY(-5px);
                svg {
                    color: var(--color-white);
                }
                &::before {
                    opacity: 1;
                }
            }
        }
    }
}
